sures sont entreprises pour venir en aide aux victimes de discrimination et d’exclusion en ce temps de crise"+_vm._s('\xa0')+"?")]),_c('p',[_vm._v("Les aînés autonomes en centre de personnes âgées sont victimes de discrimination. En effet, ils s’avèrent obligés de faire face à des mesures strictes concernant les sorties, auxquelles les autres personnes hors des centres sont indemnisées. Le gouvernement du Québec tente de remédier à la situation en clarifiant que ces mesures ne s'appliquent pas aux aînés en bonne santé "),_c('a',{staticClass:"reference-link",attrs:{"href":"https://www.ledevoir.com/societe/578307/la-revolte-des-aines?utm_medium=Social&utm_campaign=Autopost&utm_source=Facebook#Echobox=1588654595"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(".")]),_c('p',[_vm._v("La Commission des droits de la personne et des droits de la jeunesse du Québec"+_vm._s('\xa0')+" dénonce les contraventions données à des jeunes en situation d'itinérance et l'impact discriminatoire de l'interdiction de rassemblement sur les personnes en situation d'itinérance "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?idArticle=2804150115"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(". La Commission souligne que la discrimination et l'exclusion en ce temps de crise augmentent et qu'il faut donc éviter tout profilage discriminatoire "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?idArticle=2804150115"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(".")]),_c('p',[_vm._v("Précédent la crise de la COVID-19, Montréal était en pleine crise du logement. Pour éviter que les évictions abusives en temps de pandémie se poursuivent auprès de locataires vulnérables du Sud-Ouest "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&_dad=portal&_schema=PORTAL&id=32480&ret=http://ville.montreal.qc.ca/pls/portal/url/p